.form {
  &__inputs {
    margin-bottom: 32px;
  }

  &__input {
    width: 100%;

    &:not(:last-child) {
      margin-bottom: 16px;
    }
  }

  &__textarea {
    width: 100%;
    padding: 14px 16px;
    border-radius: 16px;
    min-height: 148px;
    resize: none;
  }

  &__button {
    border: none;
  }
}